import { useEffect, useRef, useState } from 'react';
import 'aplayer/dist/APlayer.min.css';
import Aplayer from 'aplayer';
import img from './img/hbt.JPG';
import coverImg from './img/cover.jpg';
import music1 from './music/music1.mp3';
import music2 from './music/music2.mp3';
import music3 from './music/music3.mp3';
import music4 from './music/music4.mp3';
import music5 from './music/music5.mp3';
import music6 from './music/music6.mp3';
import music7 from './music/music7.mp3';
import music8 from './music/music8.mp3';
import music9 from './music/music9.mp3';
import music10 from './music/music10.mp3';
import './App.css';

function App() {
  const ref = useRef(null);
  const [isInit, setIsInit] = useState(false);
  const [isListShow, setIsListShow] = useState(true);
  const [isPlay, setIsPlay] = useState(false);

  useEffect(() => {
    if (ref && ref.current) {
      if (!isInit) {
        const ap = new Aplayer({
          container: ref.current,
          autoplay: false,
          audio: [
            {
              name: '半兽人',
              artist: '周杰伦',
              url: music1,
              cover: coverImg,
            },
            {
              name: '半岛铁盒',
              artist: '周杰伦',
              url: music2,
              cover: coverImg,
            },
            {
              name: '暗号',
              artist: '周杰伦',
              url: music3,
              cover: coverImg,
            },
            {
              name: '龙拳',
              artist: '周杰伦',
              url: music4,
              cover: coverImg,
            },
            {
              name: '火车叨位去',
              artist: '周杰伦',
              url: music5,
              cover: coverImg,
            },
            {
              name: '分裂',
              artist: '周杰伦',
              url: music6,
              cover: coverImg,
            },
            {
              name: '爷爷泡的茶',
              artist: '周杰伦',
              url: music7,
              cover: coverImg,
            },
            {
              name: '回到过去',
              artist: '周杰伦',
              url: music8,
              cover: coverImg,
            },
            {
              name: '米兰的小铁匠',
              artist: '周杰伦',
              url: music9,
              cover: coverImg,
            },
            {
              name: '最后的战役',
              artist: '周杰伦',
              url: music10,
              cover: coverImg,
            },
          ],
        });
        setIsInit(true);
        console.log('ap', ap);
        ap.on('listshow', () => {
          setIsListShow(true);
        });
        ap.on('listhide', () => {
          setIsListShow(false);
        });
        ap.on('play', () => {
          setIsPlay(true);
        });
        ap.on('pause', () => {
          setIsPlay(false);
        });
      }
    }
  }, [isInit, ref]);

  const style = {
    position: 'fixed',
    width: '100%',
    height: '100%',
    left: 0,
    top: 0,
    backgroundImage: `url(${img})`,
    backgroundSize: 'cover',
  }
  
  const panStyle = {
    backgroundImage: `url(${coverImg})`,
    backgroundSize: 'cover',
    backgroundPosition: '-90px 0',
    top: isListShow ? '28%' : '50%',
  }

  if (isPlay) {
    panStyle.animation = 'circle 20s infinite linear';
  }

  return (
    <div style={style}>
      <p className='font'>for Célestine</p>
      <div className='center-pan' style={panStyle} />
      <div className='player-wrapper' ref={ref} />
    </div>
  )
}

export default App;
